.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-image: url("https://static.vecteezy.com/system/resources/previews/023/849/538/non_2x/billiard-balls-on-green-table-3d-banner-with-copy-space-vector.jpg");
  background-size: cover;
  background-color: #282c34;
  min-height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.color-filter {
  width: 100%;
  height: 100vh;
}

.flex-container {
  display: flex;
}

.point-screen {
  width: 70%;
  height: 100vh;
}

.fight {
  width: 100%;
  height: 30vh;
  background-color: rgba(10, 10, 30, 0.1);
  display: flex;
}

.fight-btn {
  padding: 20px 70px;
  margin: 20px auto;
  font-size: 30px;
  background-color: rgba(40, 44, 52, 0.5);
  color: white;
  border-radius: 10px;
}

.points {
  width: 100%;
  height: 70vh;
  background-color: rgba(10, 10, 30, 0.1);
}

.header {
  display: flex;
  padding: 10px 100px;
  font-size: 50px;
  color: white;
  background-color: rgba(10, 10, 30, 0.3);
}

.team {
  padding: 0px 40px;
  background-color: rgba(40, 44, 52, 0.5);
  color: white;
  font-size: 30px;
  border-radius: 5px;
}

p {
  margin: 10px;
  padding: 0;
}

.score {
  margin: 0px 50px;
  font-size: 40px;
}

.team-screen {
  width: 30%;
  height: 100vh;
  background-color: rgba(10, 10, 30, 0.3);
}

.makeTeam-btn {
  padding: 50px;
  margin: 50px;
  border-radius: 20px;
  background-color: #157515;
  color: white;
}

.fight1,
.fight2 {
  background-color: red;
  width: 50%;
}

.App-link {
  color: #61dafb;
}

.background {
  width: 100vw;
}
